@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
*,
*:before,
*:after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Space Mono", monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: hsl(185, 41%, 84%);
  height: 100vh;
}

.logo {
  padding-top: 2.5em;
  padding-bottom: 1em;
  display: block;
  margin: 0 auto;
}

.container {
  background-color: hsl(0, 0%, 100%);
  height: 100%;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}


.option {
  display: inline-block;
  background-color: hsl(183, 100%, 15%);
  border: none;
  text-align: center;
  font-weight: 700;
  padding: 0.5em 1.25em;
  margin: 0.5em;
  border-radius: 5px;
  color: hsl(0, 0%, 100%);
}

.option.selected {
  background-color: hsl(172, 67%, 45%);
  color: hsl(183, 100%, 15%);
}

.custom-button {
  display: inline-block;
  background-color: hsl(183, 100%, 15%);
  border: none;
  width: 80px;
  text-align: center;
  font-weight: 700;
  padding: 0.5em 1.25em;
  margin: 0.5em;
  border-radius: 5px;
  color: hsl(0, 0%, 100%);
}

.custom-percentage-input {
  background-color: hsl(202, 50%, 97%);
  width: 80px;
  padding: 0.25em 0.5em;
  text-align: center;
  border: none;
  border-radius: 5px;
  font-family: inherit;
  color: hsl(183, 100%, 15%);
  font-size: 1em;
  font-weight: 700;
}

.input-section {
  padding: 1em;
}

.input-label {
  color: hsl(186, 14%, 43%);
  display: block;
  margin-top: 1.5em;
  font-weight: 700;
}

.text-input {
  background-color: hsl(202, 50%, 97%);
  width: 100%;
  padding: 0.25em 0.5em;
  text-align: right;
  border: none;
  border-radius: 5px;
  font-family: inherit;
  font-size: 1.5em;
  font-weight: 700;
  color: hsl(183, 100%, 15%);
}

.bill-amount-text-input {
  display: flex;
}

.bill-amount-text-input::before {
  content: url(/frontend-mentors-tip-calculator-app/static/media/icon-dollar.9f9be353.svg);
  position: absolute;
  display: block;
  margin-top: 1em;
  margin-left: 0.5em;
}

.num-people-text-input::before {
  content: url(/frontend-mentors-tip-calculator-app/static/media/icon-person.65bfd204.svg);
  position: absolute;
  display: block;
  margin-top: 1em;
  margin-left: 0.5em;
}

.temp {
  border: red solid 3px;
  padding: 1em;
  margin-top: 2em;
}

.output-section {
  background-color: hsl(183, 100%, 15%);
  margin: 1em;
  padding: 1em;
  border-radius: 15px;
}

.output-title {
  color: hsl(189, 41%, 97%);
  font-weight: 700;
}

.output-flex {
  display: flex;
  justify-content: space-between;
  padding: 1em;
}

.output-subtitle {
  color: hsl(186, 14%, 43%);
}

.output-value {
  font-size: 2.5em;
  font-weight: 700;
  color: hsl(172, 67%, 45%);
}

.reset-button {
  border: none;
  padding: 0.75em 2em;
  display: block;
  margin: 2em auto 0.5em;
  width: 95%;
  border-radius: 10px;
  text-transform: uppercase;
  font-size: 1.25em;
  font-weight: 700;
  background-color: hsl(172, 67%, 45%);
  transition: all 0.2s ease-in-out;
}

.reset-button:active,
.reset-button:hover {
  background-color: hsl(183, 100%, 55%);
  transform: scale(1.03);
}

